## Mixins
--------------------------------------------------------------*/
/*
 * Sets up overall breakpoints for four settings allows for rapid in document callouts
 */
/*--------------------------------------------------------------
## Variables
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: no-repeat -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#dfe0e5));
  background: no-repeat -webkit-linear-gradient(#ffffff, #dfe0e5);
  background: no-repeat linear-gradient(#ffffff, #dfe0e5);
  /* Fallback for when there is no custom background color defined. */
  color: #28282A;
  font-family: "Open Sans", sans-serif;
  padding-top: 50px;
  height: 100vh; }
  @media screen and (min-width: 768px) {
    body {
      padding-top: 0; } }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em; }

p {
  font-size: 1rem; }
  @media (min-width: 1040px) {
    p {
      font-size: 1.125rem; } }

ul, ol {
  margin: 0 0 1.5em 3em; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5em 1.5em; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

figure {
  margin: 1em 0;
  /* Extra wide images within figure tags don't overflow the content area. */ }

table {
  margin: 0 0 1.5em;
  width: 100%; }

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.grid {
  *zoom: 1;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  margin: -15px -10px 0; }
  .grid:before, .grid:after {
    content: " ";
    display: table; }
  .grid:after {
    clear: both; }
  @media screen and (min-width: 768px) {
    .grid {
      flex-direction: row; } }

[class*='grid-col_'] {
  box-sizing: border-box;
  float: left;
  padding: 15px 10px 0;
  width: 100%; }

@media screen and (min-width: 768px) {
  .grid-col_1of12 {
    width: 8.33333%; }
  .grid-col_2of12 {
    width: 16.66667%; }
  .grid-col_3of12 {
    width: 25%; }
  .grid-col_4of12 {
    width: 33.33333%; }
  .grid-col_5of12 {
    width: 41.66667%; }
  .grid-col_6of12 {
    width: 50%; }
  .grid-col_7of12 {
    width: 58.33333%; }
  .grid-col_8of12 {
    width: 66.66667%; }
  .grid-col_9of12 {
    width: 75%; }
  .grid-col_10of12 {
    width: 83.33333%; }
  .grid-col_11of12 {
    width: 91.66667%; }
  .grid-col_12of12 {
    width: 100%; } }

.mix-grid_gapMd {
  margin: -15px -20px 0; }
  .mix-grid_gapMd [class*='grid-col_'] {
    padding: 15px 20px 0; }

.mix-grid_gapLg {
  margin: -15px -30px 0; }
  .mix-grid_gapLg [class*='grid-col_'] {
    padding: 15px 30px 0; }

